export default {
	data() {
		return {
			title: '网络请求',
			loading: false,
			res: ''
		}
	},
	onLoad() {

	},
	methods: {
		
		// 方式一
		sendRequest: function() {
			this.loading = true
			this.$api.test({noncestr: Date.now()}).then((res)=>{
				this.loading = false;
				console.log('request success', res)
				uni.showToast({
					title: '请求成功',
					icon: 'success',
					mask: true
				});
				this.res = '请求结果 : ' + JSON.stringify(res);
			}).catch((err)=>{
				this.loading = false;
				console.log('request fail', err);
			})
		},
		
		//方式二
		async sendRequest1() {
			this.loading = true
			let res = await this.$api.test({noncestr: Date.now()});
			this.loading = false;
			this.res = '请求结果 : ' + JSON.stringify(res);
		}
	}
}
